<template>
	<view>
		<view>
			<button @click="uploadImage">选取图片识别</button>
			<view class="vvv">
				<text v-model="h" selectable="true" v-for="(item,index) in content" :key="index">{{item.words}} </text>
			</view>
			<button @click="openVoice()">语音播报</button>
			<view>

			</view>

		</view>

	</view>
</template>

<script>
	//引入图片提取文字插件
	import {
		pathToBase64,
		base64ToPath
	} from '../../js_sdk/mmmm-image-tools/index.js';
	import Voice from '../../js_sdk/QuShe-baiduYY/QS-baiduyy/QS-baiduyy.js'

	export default {
		data() {
			return {
				src: '',
				content: [],
				itemNum: 1,
				h: '',
				voiceFlag: true
			}
		},
		methods: {
			uploadImage() { //   选取照片，进行OCR识别

				uni.chooseImage({
					count: 1, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					success: (res) => {
						uni.showLoading({
							title: '正在识别中...'
						});
						this.src = res.tempFilePaths[0]; //后面还能用到 src 暂且留着
						// console.log(res);
						pathToBase64(res.tempFilePaths[0])
							.then(base64 => {
								// console.log(base64)
								var base = base64.split(',')[1]
								//console.log(base)、
								// #ifdef APP-PLUS
								uni.request({
									url: 'https://aip.baidubce.com/rest/2.0/ocr/v1/accurate_basic?access_token=24.e664341193c5626807c3afaa554bdce9.2592000.1618293964.282335-23794179',
									data: {
										image: base,
										// language_type:'ENG', //识别语言类型，默认中英文结合
										detect_direction: true, //检测图像朝向
									},
									method: 'POST',
									header: {
										'Content-Type': 'application/x-www-form-urlencoded'
									},
									success: (res) => {
										this.content = res.data.words_result
										uni.hideLoading(); //把正在加载中隐藏
										console.log(res.data)
										
										//将content数组中的内容全部存到变量 h 中
										for (var i = 0; i < res.data.words_result_num; i++) {
											this.h += this.content[i].words
										}

									}

								});
								//#endif

								// #ifdef H5
								uni.request({
									url: '/api/accurate_basic?access_token=24.e664341193c5626807c3afaa554bdce9.2592000.1618293964.282335-23794179',
									data: {
										image: base,
										// language_type:'ENG', //识别语言类型，默认中英文结合
										detect_direction: true, //检测图像朝向
									},
									method: 'POST',
									header: {
										'Content-Type': 'application/x-www-form-urlencoded'
									},
									success: (res) => {
										this.content = res.data.words_result
										//获取文本条数 
										//this.itemNum = res.data.words_result_num
										uni.hideLoading(); //把正在加载中隐藏
										//console.log(res.data)
										//console.log(this.itemNum)


										//将content数组中的内容全部存到变量 h 中
										for (var i = 0; i < res.data.words_result_num; i++) {
											this.h += this.content[i].words
										}

									}

								});
								//#endif
							})
							.catch(error => {
								console.error(error)
							});

					}
				});
			},



			openVoice() {
				
					Voice(this.h)
				

			}




		}





	}
</script>

<style>
	.vvv {
		width: 99%;
		border: 2px dashed #eeeeee;
		height: 300upx;
		margin: 30upx 0 0 0;
		height: auto;
	}
</style>
